<template>
	<view class="user-info-box">
		<list-item showRight>
			<template slot="left">
				<span>用户头像</span>
			</template>
			<template slot="right">
				<button class="avatar-wrapper" open-type="chooseAvatar" @chooseavatar="onChooseAvatar">
				  <image mode="aspectFill" class="av" :src="getUrl(user.avatar)"></image>
				</button>
				<!-- <image class="av" :src="getUrl(user.avatar)"></image> -->
			</template>
		</list-item>
		<view @click="selectImage()" class="item">
			<list-item  showRight>
				<template slot="left">
					<span>主页背景</span>
				</template>
				<template slot="right">
					<span>更换主页背景</span>
				</template>
			</list-item>
		</view>
		<list-item>
			<template slot="left">
				<span>用户名</span>
			</template>
			<template slot="right">
				<view @click="open"><span>{{user.nickName}}</span></view>
			</template>
		</list-item>
		<list-item>
			<template slot="left">
				<span>联系方式</span>
			</template>
			<template slot="right">
				<view @click="open"><span>{{user.phonenumber}}</span></view>
			</template>
		</list-item>
		<list-item>
			<template slot="left">
				<span>性别</span>
			</template>
			<template slot="right">
				<span>{{getSex(user.sex)}}</span>
			</template>
		</list-item>
		
		<button style="margin: 30px 0px;" class="btn" @click="open()">修改信息</button>
		
		<uni-popup ref="popup" type="center">
			<view class="form">
				<uni-forms ref="form" :modelValue="formData" label-position="top">
					<uni-forms-item required label="昵称" name="nickName">
						<uni-easyinput type="text" v-model="formData.nickName" placeholder="请输入昵称" />
					</uni-forms-item>
					<uni-forms-item  label="联系方式" name="phonenumber">
						<uni-easyinput type="text" v-model="formData.phonenumber" placeholder="请输入联系方式" />
					</uni-forms-item>
					<uni-forms-item required  label="性别" name="sex" >
						<uni-data-checkbox  v-model="formData.sex" :localdata="sexs"/>
					</uni-forms-item>
				</uni-forms>
				<button class="btn"  @click="submitForm">修改</button>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {mapState,mapMutations} from "vuex";
	import listItem from "@/components/listItem/listItem.vue";
	import {uploadImage,updateInfo,updateBackground} from "@/api/login.js"
	import {qiniuUpload} from "@/utils/upload.js"
	export default {
		data() {
			return {
				formData:{
					nickName:"",
					sex:'2',
					phonenumber:null
					
					
				},
				
				sexs: [{
					text: '男',
					value: '0'
				}, {
					text: '女',
					value: '1'
				}, {
					text: '保密',
					value: '2'
				}],
				
				rules: {
					// 对nickName字段进行必填验证
					nickName: {
						// nickName 字段的校验规则
						rules:[
							// 校验 nickName 不能为空
							{
								required: true,
								errorMessage: '请填写昵称',
							},
							// 对nickName字段进行长度验证
							{
								minLength: 2,
								maxLength: 7,
								errorMessage: '{label}长度在 {minLength} 到 {maxLength} 个字符',
							}
						],
						// 当前表单域的字段中文名，可不填写
						label:'昵称',
						validateTrigger:'submit'
					},
					
					phonenumber: {
						
						rules:[
							
							// {
							// 	// required: true,
							// 	errorMessage: '请填写联系方式',
							// },
							
							{
								minLength: 11,
								maxLength: 11,
								errorMessage: '联系方式格式不正确',
							}
						],
						// 当前表单域的字段中文名，可不填写
						label:'联系方式',
						validateTrigger:'submit'
					}
				}
			}
		},
		onReady() {
				// 需要在onReady中设置规则
			this.$refs.form.setRules(this.rules)
		},
		components:{
			listItem
		},
		computed:{
			...mapState('user',['user'])
		},
		methods: {
			selectImage(){
				wx.chooseMedia({
				  count:1,
				  mediaType: ['image'],
				  sourceType: ['album', 'camera'],
				  camera: 'back',
				  success(res) {
					const name = res.tempFiles[0].tempFilePath.split('/').pop()
					qiniuUpload(res.tempFiles[0].tempFilePath,'background/' + name).then(res2=>{
						updateBackground(res2.imageURL).then(()=>{
							uni.showToast({
								icon:"none",
								title:"修改成功,请返回下拉刷新页面",
								duration:2000
							})
						})
					})
				  }
				})
			},
			...mapMutations('user',['SET_USER']),
			onChooseAvatar(e) {
				let { avatarUrl } = e.detail 
				const file = avatarUrl
				const name = avatarUrl.split('/').pop()
				qiniuUpload(file,'avatar/' + name).then(res=>{
					
					const param = {	
						avatar : res.imageURL			
					}
					updateInfo(param).then(()=>{
						uni.showToast({
							title:"修改成功",
							duration:2000
						});		
					
						let user = {
							...this.user
						};
						user.avatar = res.imageURL;
						this.$store.commit("user/SET_USER",user)
					
					})
				})
			
			 },
			getSex(sex){
				if(sex==0)
					return '男';
				else if(sex==1)
					return '女';
				return "保密"
			},
			open(){
				  this.formData.nickName = this.user.nickName
				  this.formData.sex = this.user.sex
				  this.formData.phonenumber = this.user.phonenumber
			        // 通过组件定义的ref调用uni-popup方法 ,如果传入参数 ，type 属性将失效 ，仅支持 ['top','left','bottom','right','center']
				  // console.log(this.formData)	
			      this.$refs.popup.open('center')
			},
			submitForm(){
				
				this.$refs.form.validate().then((res)=>{
						this.$store.dispatch("user/updateUserInfo",this.formData)
						this.$refs.popup.close()
		// 				updateInfo(this.formData).then(()=>{
		// 					this.$refs.popup.close()
		// 					let user = {
		// 						...this.user
		// 					}
		// 					user.nickName = this.formData.nickName
		// 					user.phonenumber = this.formData.phonenumber
		// 					user.sex = this.formData.sex	
		
		// 					this.$store.commit("user/SET_USER",user)
		// 					uni.showToast({
		// 						title:"修改成功",
		// 						duration:2000
		// 					});
							
		// 				}).catch((err)=>{
							
		// 				})
					}).catch(err =>{
						// console.log('表单错误信息：', err);
					})
				// console.log(this.formData)
				
			}
		}
	}
</script>

<style>
.user-info-box .av{
	width: 50px;
	height: 50px;
	border-radius: 50%;
}
.avatar-wrapper{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;

	
}
button::after {
   border: none;
}
.form{
	padding: 15px 30px;
	background-color: white;
}


</style>
